import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';


// При возникновешение ошибок необходимо установить bootstrap 

// Команда npm install bootstrap --save 
// npm i bootstrap

// В папках с package.json

// import './bootstrap/css/bootstrap.min.css';
// import './bootstrap/js/bootstrap.min';

// Подгружено из npm install bootstrap 
// Отличаются цветом: Вверхнее серый шрифт, нижнее черный

// При npm run build нужно использовать эти данные, иначе будет выведена ошибка
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min';

// Для установки bootstrap-icons необходимо выполнить следующие команды

// Команды: cd ..

// npm install bootstrap bootstrap-icons

import 'bootstrap-icons/font/bootstrap-icons.css';

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
