import { BrowserRouter, Routes, Route } from 'react-router-dom'; 

function App() {
  return (<BrowserRouter>
    <Routes>
      <Route path='*' element={<Base />} />
      <Route path='login' element={<Login />}/>
    </Routes>
  </BrowserRouter>);
}

export default App;
