import { useMemo, useState } from 'react';
import './Login.css';

import axios from "axios";
import { useNavigate } from 'react-router-dom';

function Login() {

    // При построении билда необходимо указывать эти модули доступа csrf-токены

    // npm run build 
    
    axios.defaults.xsrfCookieName = 'csrftoken';
    axios.defaults.xsrfHeaderName = 'X-CSRFToken';

    const navigate = useNavigate();

    // Автоматический подстраивающаяся URL для dev-мода и продакшена
    const basicURL = process.env.NODE_ENV === 'development' ? 'http://localhost:8000' : ''

    useMemo(() => {
        // Чтобы использовался глобальный путь прописываем /check-user/, а не check-user/
        // Тогда скомпилированный фронт будет обращаться к login/check-user/
        axios.get(basicURL + "/check-user/", 

            {
                headers: {
                'Content-Type': 'application/json',
                }

            }
            ).then(response => {
                
                if(response.data.user.username !== 'AnonymousUser'){
                    navigate('/');
                }

            }).catch((error) => {
            console.log("error", error)

            alert('Ошибка: ' + error);

            });

    }, [navigate]);

    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');

    const usernameHandler = (event) => {
        
        setUsername(event.target.value);

    }

    const passwordHandler = (event) => {
        
        setPassword(event.target.value);

    }

    const loginSubmitHandler = (event) => {
        
        event.preventDefault();

        if (username.length === 0) {

            alert('Ошибка: Введите логин');
            return;
        }

        if (password.length === 0) {

            alert('Ошибка: Введите пароль');
            return;
        }

        // Важным моментом является передача параметров. Если в POST-запросе нет json-данных необходимо обязательно указать {}, иначе headers будут восприниматься как data
        
        axios.post(basicURL + "/login-user/", 
            {
                'username': username,
                'password': password
            },

            {
                headers: {
                'Content-Type': 'application/json',
                }

            }
            ).then(response => {

                navigate('/');

            }).catch((error) => {
            console.log("error", error.response.data.error_message)
                
            alert('Ошибка: ' + error.response.data.error_message);

            });


    }

    return (<div className="my_container">
		<div className="login-form">
			<div className="form-header">
				<text>Вход в локальную систему управления</text>
			</div>

			<form onSubmit={loginSubmitHandler}>
                <input type="text" name="username" placeholder="Логин" value={username} onChange={usernameHandler} />

                <input type="password" name="password" placeholder="Пароль" value={password} onChange={passwordHandler} />

				<input type="button" name="submitButton" value="Войти в систему" onClick={loginSubmitHandler}/>

			</form>
		</div>
	</div>
);
}


export default Login;